.flex-row {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.date {
  margin-bottom: 40px;
  margin-top: -15px;
}

.box-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;
}

.box-wrapper .box {
  background-color: yellow;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}

.box-wrapper .big-box {
  height: 100px;
  width: 100px;
}

.box-wrapper .small-box {
  height: 50px;
  width: 50px;
}

table {
  width: 100%;
  border-spacing: 10px;
}

table td {
  padding: 0;
}

table .bar {
  width: 80%;
  height: 2rem;
  background-color: #f1f1f1;
  position: relative;
  border-radius: 5px;
}

table .bar .color-bar {
  height: 2rem;
  border-radius: 5px;
}

.biomass {
  background-color: lightgreen;
}
.coal {
  background-color: rgb(16, 46, 16);
}
.gas {
  background-color: rgb(147, 179, 88);
}
.geothermal {
  background-color: rgb(173, 103, 45);
}
.hydro {
  background-color: rgb(153, 49, 139);
}
.nuclear {
  background-color: rgb(103, 116, 103);
}
.oil {
  background-color: rgb(75, 10, 13);
}
.solar {
  background-color: rgb(138, 137, 204);
}
.wind {
  background-color: rgb(31, 5, 180);
}
.unknown {
  background-color: rgb(3, 80, 3);
}

.popup {
  display: none;
  position: absolute;
  top: -50%;
  left: 50%;
  background-color: #efd1d1;
  z-index: 1;
  padding: 20px;
  font-size: 1rem;
}

table .bar:hover .popup {
  display: block;
}
