.carbon-bar {
  width: 300px;
  height: 20px;
  margin-bottom: 100px;
  position: relative;
}

.carbon-bar .bar-0,
.carbon-bar .bar-200,
.carbon-bar .bar-400,
.carbon-bar .bar-600,
.carbon-bar .bar-800 {
  position: absolute;
  bottom: -150%;
}

.carbon-bar .bar-0 {
  left: -2%;
}
.carbon-bar .bar-200 {
  left: 20%;
}
.carbon-bar .bar-400 {
  left: 45%;
}
.carbon-bar .bar-600 {
  left: 70%;
}
.carbon-bar .bar-800 {
  right: -5%;
}
